import Link from "next/link";

export default function Home() {
    return (
        <div className="min-h-screen py-12 px-4 md:py-20 flex flex-col items-center">
            {/* Minimalist Card */}
            <div className="ana-card text-center space-y-8 w-full">
                {/* Header Section */}
                <div className="space-y-4">
                    <p className="uppercase tracking-[0.3em] text-[#64748b] text-sm font-semibold">
                        Web Oficial
                    </p>
                    <h1 className="text-3xl md:text-4xl font-semibold text-[#2d3e50]">
                        Ana Katherine Zepeda Pallacán
                    </h1>
                    <p className="italic text-[#64748b] text-base md:text-lg">
                        Psicóloga – Maestra de Yoga – Meditación – Crioterapia
                    </p>
                </div>

                <div className="h-[1px] bg-[#f1f5f9] w-full" />

                {/* Mission Section */}
                <div className="space-y-6 text-[#1e293b] text-lg leading-relaxed">
                    <p>
                        Navegando todo sobre salud mental, física y espiritual.
                    </p>
                    <p>
                        <span className="font-bold">Desde 2009 a la fecha</span>, convencida de que menos es más.
                    </p>
                </div>

                {/* Registration Box */}
                <div className="dotted-box space-y-6">
                    <h2 className="uppercase tracking-[0.1em] text-[#1e293b] font-bold text-lg">
                        Regístrate hoy y recibe:
                    </h2>
                    <ul className="text-left space-y-4 text-sm md:text-base text-[#475569] max-w-lg mx-auto">
                        <li className="flex items-start">
                            <span className="text-[#2d3e50] mr-3">•</span>
                            <span>Audio para aprender a meditar.</span>
                        </li>
                        <li className="flex items-start">
                            <span className="text-[#2d3e50] mr-3">•</span>
                            <span>Datos prácticos de salud.</span>
                        </li>
                        <li className="flex items-start">
                            <span className="text-[#2d3e50] mr-3">•</span>
                            <span>Reflexiones para que no sientas que eres el único loco o loca que busca algo mejor de lo que hemos creado.</span>
                        </li>
                        <li className="flex items-start">
                            <span className="text-[#2d3e50] mr-3">•</span>
                            <span>Acceso anticipado a lanzamientos, regalos y promociones.</span>
                        </li>
                    </ul>
                </div>

                {/* Form Section */}
                <div className="pt-6 space-y-8 max-w-md mx-auto">
                    <p className="font-semibold text-[#2d3e50]">Te leo</p>

                    <form className="space-y-4">
                        <input
                            type="text"
                            placeholder="Tu nombre"
                            className="input-ana"
                            required
                        />
                        <input
                            type="email"
                            placeholder="Tu correo electrónico"
                            className="input-ana"
                            required
                        />
                        <button type="submit" className="btn-ana w-full">
                            Registrarme
                        </button>
                    </form>
                </div>

                {/* Simple Login Link */}
                <div className="pt-8 border-t border-[#f1f5f9]">
                    <Link href="/login" className="text-xs text-[#94a3b8] hover:text-[#2d3e50] transition-colors uppercase tracking-widest font-semibold">
                        Acceso Alumnos
                    </Link>
                </div>
            </div>

            <footer className="mt-12 text-center text-xs text-[#94a3b8] font-medium tracking-tight">
                © 2026 Manay Academy. Todos los derechos reservados.
            </footer>
        </div>
    );
}
